// getters
const getters = {

}

const actions = {

}

const mutations = {
    close(state, data) {
        if(data.name && state.items.hasOwnProperty(data.name)) {
            state.items[data.name].show = false;
            document.getElementsByTagName('body')[0].classList.remove("nonscroll");
            setTimeout(function(){
              let header = document.getElementById('block-header');
              header.classList.remove('out');
              header.classList.remove('h-fixed');
            },100);
        }
    },
    open(state, data) {
        if(data.name && state.items.hasOwnProperty(data.name)) {
            state.items[data.name].show = true
        }
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}
